"use client";

import React, { useState, useEffect } from "react";
import { format } from "date-fns";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter } from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Button } from "@/components/ui/button";
import { Calendar, Clock, User, Mail, Phone, MessageSquare } from "lucide-react";

// Example random titles
const randomTitles = ["Consultation", "Follow-up Appointment"];

function convertTimeToAmPm(time24) {
  // time24 is usually "HH:MM" from <input type="time">
  // Example: "14:30" -> "2:30 PM"
  if (!time24) return "";
  const [hourStr, minuteStr] = time24.split(":");
  let hour = parseInt(hourStr, 10);
  const minute = parseInt(minuteStr, 10);
  const suffix = hour >= 12 ? "PM" : "AM";

  if (hour === 0) {
    hour = 12; // Midnight is 12 AM
  } else if (hour > 12) {
    hour -= 12; // Convert to 12-hour format
  }

  return `${hour}:${minute.toString().padStart(2, "0")} ${suffix}`;
}

export default function AppointmentModal({ doctor, isOpen, onClose }) {
  const [date, setDate] = useState("");
  const [time24, setTime24] = useState("");
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    contact: "",
    description: "",
  });

  useEffect(() => {
    // Reset form each time modal opens
    if (!isOpen) {
      setDate("");
      setTime24("");
      setFormData({ name: "", email: "", contact: "", description: "" });
    }
  }, [isOpen]);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    if (!date || !time24 || !formData.name) {
      alert("Please fill out date, time, and name fields.");
      return;
    }

    // Convert the 24-hour input to AM/PM
    const timeAmPm = convertTimeToAmPm(time24);

    // Pick a random title pattern
    const randomTitle =
      randomTitles[Math.floor(Math.random() * randomTitles.length)];

    // Build a consistent appointment object
    const newAppointment = {
      id: Date.now(),
      // Example: "Consultation with Dr. Smith"
      title: `${randomTitle} with Dr. ${doctor?.name || "Unknown"}`,
      doctorName: `Dr. ${doctor?.name || "Unknown"}`,
      // Store just the date string (e.g., "2025-03-15") so Dashboard can parse it
      appointmentDate: date,
      // Store "HH:MM AM/PM" for your Dashboard's time parsing
      time: timeAmPm,
      // For patients, store the patient's name (from the form) if desired
      patientName: formData.name,
      // Optional: store extra fields if you need them
      email: formData.email,
      contact: formData.contact,
      notes: formData.description,
      status: "pending",
    };

    // Save to localStorage
    const storedAppointments = JSON.parse(localStorage.getItem("appointments")) || [];
    storedAppointments.push(newAppointment);
    localStorage.setItem("appointments", JSON.stringify(storedAppointments));

    // Close modal and reset
    onClose();
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-lg">
        <DialogHeader>
          <DialogTitle>Book Appointment</DialogTitle>
          <DialogDescription>
            {doctor ? `Book with Dr. ${doctor.name}` : "Complete the form to book your appointment."}
          </DialogDescription>
        </DialogHeader>

        <form onSubmit={handleSubmit}>
          <div className="grid gap-4 py-4">
            {/* Patient Name */}
            <div className="grid grid-cols-4 items-center gap-4">
              <Label htmlFor="name" className="text-right">
                <User className="inline-block mr-1 w-4 h-4 text-gray-500" />
                Name
              </Label>
              <Input
                id="name"
                name="name"
                value={formData.name}
                onChange={handleInputChange}
                placeholder="Your Name"
                required
                className="col-span-3"
              />
            </div>

            {/* Email */}
            <div className="grid grid-cols-4 items-center gap-4">
              <Label htmlFor="email" className="text-right">
                <Mail className="inline-block mr-1 w-4 h-4 text-gray-500" />
                Email
              </Label>
              <Input
                id="email"
                name="email"
                type="email"
                value={formData.email}
                onChange={handleInputChange}
                placeholder="you@example.com"
                className="col-span-3"
              />
            </div>

            {/* Contact */}
            <div className="grid grid-cols-4 items-center gap-4">
              <Label htmlFor="contact" className="text-right">
                <Phone className="inline-block mr-1 w-4 h-4 text-gray-500" />
                Contact
              </Label>
              <Input
                id="contact"
                name="contact"
                value={formData.contact}
                onChange={handleInputChange}
                placeholder="(123) 456-7890"
                className="col-span-3"
              />
            </div>

            {/* Date */}
            <div className="grid grid-cols-4 items-center gap-4">
              <Label htmlFor="date" className="text-right">
                <Calendar className="inline-block mr-1 w-4 h-4 text-gray-500" />
                Date
              </Label>
              <Input
                id="date"
                name="date"
                type="date"
                value={date}
                onChange={(e) => setDate(e.target.value)}
                required
                className="col-span-3"
              />
            </div>

            {/* Time */}
            <div className="grid grid-cols-4 items-center gap-4">
              <Label htmlFor="time" className="text-right">
                <Clock className="inline-block mr-1 w-4 h-4 text-gray-500" />
                Time
              </Label>
              <Input
                id="time"
                name="time"
                type="time"
                value={time24}
                onChange={(e) => setTime24(e.target.value)}
                required
                className="col-span-3"
              />
            </div>

            {/* Additional Notes */}
            <div className="grid grid-cols-4 items-start gap-4">
              <Label htmlFor="description" className="text-right mt-2">
                <MessageSquare className="inline-block mr-1 w-4 h-4 text-gray-500" />
                Notes
              </Label>
              <Textarea
                id="description"
                name="description"
                placeholder="Describe your symptoms or reason for visit"
                value={formData.description}
                onChange={handleInputChange}
                className="col-span-3"
                rows={3}
              />
            </div>
          </div>

          <DialogFooter>
            <Button type="button" variant="outline" onClick={onClose}>
              Cancel
            </Button>
            <Button type="submit" disabled={isSubmitting}>
              {isSubmitting ? "Booking..." : "Confirm Booking"}
            </Button>
          </DialogFooter>
        </form>
      </DialogContent>
    </Dialog>
  );
}
